







































let id = 1000;
import {TableToolbar} from "@/components";
import {Component,Vue} from "vue-property-decorator"
@Component({
  components:{
    TableToolbar
  }
})
export default class Organize extends Vue{
  data:any[]=[
        {
          F_ID: "1",
          F_ParentId: "0",
          F_FullName: "层级1",
          children: [
            {
              F_ID: "2",
              F_ParentId: "1",
              F_FullName: "层级2",
              children: [
                {
                  F_ID: "3",
                  F_ParentId: "2",
                  F_FullName: "层级3",
                  children: [
                    {
                      F_ID: "4",
                      F_ParentId: "3",
                      F_FullName: "层级4",
                      children: [],
                      route: null,
                      Root: "1",
                      OrderIndex: null
                    },
                    {
                      F_ID: "5",
                      F_ParentId: "3",
                      F_FullName: "层级4",
                      children: [],
                      route: null,
                      Root: "1",
                      OrderIndex: null
                    },
                    {
                      F_ID: "6",
                      F_ParentId: "3",
                      F_FullName: "层级4",
                      children: [],
                      route: null,
                      Root: "1",
                      OrderIndex: null
                    }
                  ],
                  route: null,
                  Root: "-1",
                  OrderIndex: null
                }
              ],
              route: null,
              Root: "-1",
              OrderIndex: null
            }
          ],
          route: null,
          Root: "-1",
          OrderIndex: null
        }
  ]
  defaultProps:any={
    label: "F_FullName",
    children: "children"
  }
  obj:any={}
  keyWord:string=""

  append(data) {
    const newChild = { id: id++, label: "testtest", children: [] };
    if (!data.children) {
      this.$set(data, "children", []);
    }
    data.children.push(newChild);
  }

  remove(node, data) {
    const parent = node.parent;
    const children = parent.data.children || parent.data;
    const index = children.findIndex(d => d.id === data.id);
    children.splice(index, 1);
  }

  handleCheck() {
    alert(1);
  }

  handleClick() {
    this.$set(this.obj, "name", "hansu");
  }
}

